<template>
  <div>

    <div style="margin: 10px 0">
      <el-input style="width: 200px " placeholder="请输入菜单名称" suffix-icon="el-icon-search"
                v-model="name"></el-input>

      <el-button class="ml-5" type="primary" @click="load()">搜索</el-button>
      <el-button type="primary" @click="reset()">重置</el-button>
    </div>
    <div style="margin: 10px 0;">
      <el-button type="primary" @click="handleAdd()">新增<i class="el-icon-circle-plus-outline"
                                                          style="margin-left: 5px"></i></el-button>
      <el-button type="danger" @click="delbath()">批量删除<i class="el-icon-remove" style="margin-left: 5px"></i>
      </el-button>
      <el-upload
          class="upload-demo ml-10"
          action="http://localhost:9090/role/import" :show-file-list="false" accept=".xlsx" :on-success="handleExcelImportSuccess" style="display: inline-block;">
        <el-button type="primary" > 导入<i class="el-icon-bottom" style="margin-left: 5px"></i>
        </el-button>
      </el-upload>
      <el-button type="primary" @click="exp()" class="ml-10">导出<i class="el-icon-top" style="margin-left: 5px"></i></el-button>
    </div>
    <el-table :data="tableData" border stripe :header-cell-class-name="headerBg"
              row-key="id" default-expand-all @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="id" label="ID" width="80"></el-table-column>
      <el-table-column prop="name" label="名称" ></el-table-column>
      <el-table-column prop="path" label="路径" ></el-table-column>
      <el-table-column prop="pagePath" label="页面路径" ></el-table-column>
      <el-table-column prop="icon" label="图标" align="center">
        <template slot-scope="scope">
          <i :class="scope.row.icon" style="font-size: 20px"></i>
        </template>
      </el-table-column>
      <el-table-column prop="description" label="描述"></el-table-column>
      <el-table-column label="操作" width="280" v-slot="scope" min-width="83px" align="center" style="  display:inline-block;" >
        <el-button type="primary" @click="handleAdd(scope.row.id) " v-if="!scope.row.pid && !scope.row.path ">新增子菜单<i class="el-icon-plus" style="margin-left: 3px"></i></el-button>
        <el-button type="success" @click="handleEdit(scope.row)">编辑<i class="el-icon-edit" style="margin-left: 3px"></i></el-button>
        <el-button type="danger" @click="handleDelete(scope.row.id)" style="margin-left: 0">删除<i class="el-icon-remove" style="margin-left: 3px"></i></el-button>
      </el-table-column>
    </el-table>
    <el-dialog title="菜单信息" :visible.sync="dialogFormVisible" width="30%">
      <el-form label-width="80px" size="small">
        <el-form-item label="名称">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="路径">
          <el-input v-model="form.path" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="页面路径">
          <el-input v-model="form.pagePath" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="图标">
         <el-select clearable v-model="form.icon" placeholder="请选择" style="width: 100%">
           <el-option v-for="item in options" :key="item.name" :label="item.name" :value="item.value">
             <i :class="item.value" />{{ item.name }}
           </el-option>
         </el-select>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="form.description" autocomplete="off"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>

    </el-dialog>
  </div>
</template>

<script>
import {serverIp} from "../../public/config";

export default {
  name: "Role",
  data() {
    return {
      tableData: [],
      total: 0,
      pageNum: 1,
      pageSize: 10,
      name: "",
      description: "",
      dialogFormVisible: false,
      form: {},
      multipleSelection: [],
      headerBg: 'headerBg',
      options:[]
    }
  },
  created() {//页面创建之后
    //请求分页查询数据
    this.load()
  },
  methods: {
    load() {
      this.request.get("/menu", {
        params: {
          name: this.name,
        }
      }).then(res => {
        this.tableData = res.data
      })

    },
    handleSizeChange(pageSize) {
      console.log(pageSize)
      this.pageSize = pageSize
      this.load()
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum)
      this.pageNum = pageNum
      this.load()
    },
    reset() {
      this.name = ""
      this.description = ""
      this.load()
    },
    handleAdd(pid) {
      this.dialogFormVisible = true
      this.form = {}
      if (pid){
        this.form.pid = pid;
      }
    },
    save() {
      this.request.post("/menu", this.form).then(res => {
        if (res) {
          console.log("this.form.icon==>"+JSON.stringify(this.form))
          this.$message.success("保存成功")
          this.dialogFormVisible = false
          this.load()
        } else {
          this.$message.success("保存失败")
        }
      })

    },
    handleEdit(row) {
      this.form = row
      console.log("this.form==>" + this.form)
      this.dialogFormVisible = true

      this.request.get("/menu/icons").then(res => {
        this.options = res.data
      })
    },
    handleCancel() {
      this.dialogFormVisible = false
      this.load()
    },
    handleDelete(id) {
      this.$confirm('此操作将永久删除该用户信息, 是否继续?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.request.delete("/menu/" + id).then(res => {
          console.log("delRes==>" + res)
          this.load()
        })
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
    },
    handleSelectionChange(val) {
      console.log(val)
      this.multipleSelection = val
    },
    delbath() {
      this.$confirm('此操作将永久删除该用户信息, 是否继续?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let ids = this.multipleSelection.map(v => v.id)
        this.request.post("/menu/del/batch", ids).then(res => {
          console.log("delRes==>" + res)
          if (res == true) {
            this.$message({
              type: 'success',
              message: '删除成功!',
            })
            this.load()
          } else {
            this.$message({
              type: 'info',
              message: '请至少选择一个用户进行删除'
            })
          }

        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },

    //导入用户信息成功提示
    handleExcelImportSuccess(){
      this.$message.success("用户信息上传成功!")
      this.load()
    },
    //导出用户信息
    exp() {
      window.open(`http://${serverIp}:9090/role/export`)
    },
  }
}

</script>
<style>
.headerBg {
  background-color: #eee !important;
}
</style>

